module.exports = "// tslint:disable:no-any\nimport { Component } from '@angular/core';\n\nconst options = [{\n  value: 'zhejiang',\n  label: 'Zhejiang',\n  children: [{\n    value: 'hangzhou',\n    label: 'Hangzhou',\n    children: [{\n      value: 'xihu',\n      label: 'West Lake',\n      isLeaf: true\n    }]\n  }, {\n    value: 'ningbo',\n    label: 'Ningbo',\n    isLeaf: true\n  }]\n}, {\n  value: 'jiangsu',\n  label: 'Jiangsu',\n  children: [{\n    value: 'nanjing',\n    label: 'Nanjing',\n    children: [{\n      value: 'zhonghuamen',\n      label: 'Zhong Hua Men',\n      isLeaf: true\n    }]\n  }]\n}];\n\n@Component({\n  selector: 'nz-demo-cascader-size',\n  template: `\n    <nz-cascader\n      [nzSize]=\"'large'\"\n      [nzOptions]=\"nzOptions\"\n      [(ngModel)]=\"value1\"\n      (ngModelChange)=\"onChanges($event)\">\n    </nz-cascader>\n    <nz-cascader\n      [nzOptions]=\"nzOptions\"\n      [(ngModel)]=\"value2\"\n      (ngModelChange)=\"onChanges($event)\">\n    </nz-cascader>\n    <nz-cascader\n      [nzSize]=\"'small'\"\n      [nzOptions]=\"nzOptions\"\n      [(ngModel)]=\"value3\"\n      (ngModelChange)=\"onChanges($event)\">\n    </nz-cascader>\n  `,\n  styles  : [\n    `\n    .ant-cascader-picker {\n      width: 300px;\n      margin-bottom: 8px;\n    }\n    `\n  ]\n})\nexport class NzDemoCascaderSizeComponent {\n  /** init data */\n  nzOptions = options;\n\n  /** ngModel value */\n  public value1: any[] = null;\n  public value2: any[] = null;\n  public value3: any[] = null;\n\n  public onChanges(values: any): void {\n    console.log(values);\n  }\n}\n"