module.exports = "// tslint:disable:no-any\nimport { Component } from '@angular/core';\n\nconst provinces = [{\n  value: 'zhejiang',\n  label: 'Zhejiang'\n}, {\n  value: 'jiangsu',\n  label: 'Jiangsu'\n}];\n\nconst cities = {\n  zhejiang: [{\n    value: 'hangzhou',\n    label: 'Hangzhou'\n  }, {\n    value: 'ningbo',\n    label: 'Ningbo',\n    isLeaf: true\n  }],\n  jiangsu: [{\n    value: 'nanjing',\n    label: 'Nanjing'\n  }]\n};\n\nconst scenicspots = {\n  hangzhou: [{\n    value: 'xihu',\n    label: 'West Lake',\n    isLeaf: true\n  }],\n  nanjing: [{\n    value: 'zhonghuamen',\n    label: 'Zhong Hua Men',\n    isLeaf: true\n  }]\n};\n\n@Component({\n  selector: 'nz-demo-cascader-default-value-and-lazyload',\n  template: `\n    <nz-cascader\n      [(ngModel)]=\"values\"\n      [nzLoadData]=\"loadData\"\n      (ngModelChange)=\"onChanges($event)\">\n    </nz-cascader>\n  `,\n  styles  : [\n    `\n    .ant-cascader-picker {\n      width: 300px;\n    }\n    `\n  ]\n})\nexport class NzDemoCascaderDefaultValueAndLazyloadComponent {\n\n  /** ngModel value */\n  public values: any[] = ['zhejiang', 'hangzhou', 'xihu'];\n\n  public onChanges(values: any): void {\n    console.log(values, this.values);\n  }\n\n  /** load data async execute by `nzLoadData` method */\n  public loadData(node: any, index: number): PromiseLike<any> {\n    return new Promise((resolve) => {\n      setTimeout(() => {\n        if (index < 0) { // if index less than 0 it is root node\n          node.children = provinces;\n        } else if (index === 0) {\n          node.children = cities[node.value];\n        } else {\n          node.children = scenicspots[node.value];\n        }\n        resolve();\n      }, 1000);\n    });\n  }\n\n}\n"