module.exports = "// tslint:disable:no-any\nimport { Component, OnInit } from '@angular/core';\n\nconst options = [{\n  value: 'zhejiang',\n  label: 'Zhejiang',\n  children: [{\n    value: 'hangzhou',\n    label: 'Hangzhou',\n    children: [{\n      value: 'xihu',\n      label: 'West Lake',\n      isLeaf: true\n    }]\n  }, {\n    value: 'ningbo',\n    label: 'Ningbo',\n    isLeaf: true\n  }]\n}, {\n  value: 'jiangsu',\n  label: 'Jiangsu',\n  children: [{\n    value: 'nanjing',\n    label: 'Nanjing',\n    children: [{\n      value: 'zhonghuamen',\n      label: 'Zhong Hua Men',\n      isLeaf: true\n    }]\n  }]\n}];\n\n@Component({\n  selector: 'nz-demo-cascader-default-value-and-asyn-options',\n  template: `\n    <nz-cascader\n      [(ngModel)]=\"values\"\n      [nzOptions]=\"nzOptions\"\n      (ngModelChange)=\"onChanges($event)\">\n    </nz-cascader>\n  `,\n  styles  : [\n    `\n    .ant-cascader-picker {\n      width: 300px;\n    }\n    `\n  ]\n})\nexport class NzDemoCascaderDefaultValueAndAsynOptionsComponent implements OnInit {\n  /** init data */\n  public nzOptions = null;\n\n  /** ngModel value */\n  public values: any[] = ['zhejiang', 'hangzhou', 'xihu'];\n\n  public onChanges(values: any): void {\n    console.log(values, this.values);\n  }\n\n  ngOnInit(): void {\n    setTimeout(() => {\n      this.nzOptions = options;\n    }, 500);\n  }\n\n}\n"