module.exports = "// tslint:disable:no-any\nimport { Component } from '@angular/core';\n\nconst options = [{\n  value: 'zhejiang',\n  label: 'Zhejiang',\n  children: [{\n    value: 'hangzhou',\n    label: 'Hangzhou',\n    children: [{\n      value: 'xihu',\n      label: 'West Lake',\n      isLeaf: true\n    }]\n  }, {\n    value: 'ningbo',\n    label: 'Ningbo',\n    isLeaf: true\n  }]\n}, {\n  value: 'jiangsu',\n  label: 'Jiangsu',\n  children: [{\n    value: 'nanjing',\n    label: 'Nanjing',\n    children: [{\n      value: 'zhonghuamen',\n      label: 'Zhong Hua Men',\n      isLeaf: true\n    }]\n  }]\n}];\n\n@Component({\n  selector: 'nz-demo-cascader-trigger',\n  template: `\n    {{text}}\n    <nz-cascader\n      [nzShowInput]=\"false\"\n      [nzOptions]=\"nzOptions\"\n      [(ngModel)]=\"values\"\n      (ngModelChange)=\"onChanges($event)\"\n      (nzSelectionChange)=\"onSelectionChange($event)\"\n      >\n      <a href=\"javascript: void(0)\">Change city</a>\n    </nz-cascader>`,\n  styles  : [\n    `\n    .ant-cascader-picker {\n      width: 300px;\n    }\n    `\n  ]\n})\nexport class NzDemoCascaderTriggerComponent {\n  /** init data */\n  nzOptions = options;\n\n  /** ngModel value */\n  public values: any[] = null;\n\n  public text = 'Unselect';\n\n  public onChanges(values: any): void {\n    console.log(values, this.values);\n  }\n\n  public onSelectionChange(selectedOptions: any[]): void {\n    this.text = selectedOptions.map(o => o.label).join(', ');\n  }\n}\n"