module.exports = "// tslint:disable:no-any\nimport { Component } from '@angular/core';\nimport { FormBuilder, FormGroup, Validators } from '@angular/forms';\n\nconst options = [ {\n  value   : 'zhejiang',\n  label   : 'Zhejiang',\n  children: [ {\n    value   : 'hangzhou',\n    label   : 'Hangzhou',\n    children: [ {\n      value : 'xihu',\n      label : 'West Lake',\n      isLeaf: true\n    } ]\n  }, {\n    value : 'ningbo',\n    label : 'Ningbo',\n    isLeaf: true\n  } ]\n}, {\n  value   : 'jiangsu',\n  label   : 'Jiangsu',\n  children: [ {\n    value   : 'nanjing',\n    label   : 'Nanjing',\n    children: [ {\n      value : 'zhonghuamen',\n      label : 'Zhong Hua Men',\n      isLeaf: true\n    } ]\n  } ]\n} ];\n\n@Component({\n  selector: 'nz-demo-cascader-reactive-form',\n  template: `\n    <form [formGroup]=\"form\" novalidate>\n      <nz-cascader\n        [nzOptions]=\"nzOptions\"\n        (nzChange)=\"onChanges($event)\"\n        [formControlName]=\"'name'\">\n      </nz-cascader>\n    </form>\n    <br>\n    <button nz-button (click)=\"reset()\">Reset</button>\n    <button nz-button (click)=\"submit()\">Submit</button>\n  `,\n  styles  : [\n      `\n      .ant-cascader-picker {\n        width: 300px;\n      }\n\n      button {\n        margin-right: 8px;\n      }\n    `\n  ]\n})\nexport class NzDemoCascaderReactiveFormComponent {\n  /** init data */\n  nzOptions = options;\n\n  public form: FormGroup;\n\n  constructor(private fb: FormBuilder) {\n    this.createForm();\n  }\n\n  private createForm(): void {\n    this.form = this.fb.group({\n      name: [ null, Validators.required ]\n    });\n  }\n\n  public reset(): void {\n    this.form.reset();\n    console.log(this.form.value);\n  }\n\n  public submit(): void {\n    console.log(this.form.value);\n  }\n\n  public onChanges(values: any): void {\n    console.log(values);\n  }\n}\n"