module.exports = "// tslint:disable:no-any\nimport { Component } from '@angular/core';\n\nconst options = [{\n  value: 'zhejiang',\n  label: 'Zhejiang',\n  children: [{\n    value: 'hangzhou',\n    label: 'Hangzhou',\n    children: [{\n      value: 'xihu',\n      label: 'West Lake',\n      isLeaf: true\n    }]\n  }, {\n    value: 'ningbo',\n    label: 'Ningbo',\n    isLeaf: true\n  }]\n}, {\n  value: 'jiangsu',\n  label: 'Jiangsu',\n  children: [{\n    value: 'nanjing',\n    label: 'Nanjing',\n    children: [{\n      value: 'zhonghuamen',\n      label: 'Zhong Hua Men',\n      isLeaf: true\n    }]\n  }]\n}];\n\n@Component({\n  selector: 'nz-demo-cascader-modal',\n  template: `\n    <nz-modal [(nzVisible)]=\"isVisible\" nzTitle=\"Please select\" (nzOnCancel)=\"handleCancel($event)\" (nzOnOk)=\"handleOk($event)\">\n      <nz-cascader\n        [nzOptions]=\"nzOptions\"\n        [(ngModel)]=\"values\"\n        (ngModelChange)=\"onChanges($event)\">\n      </nz-cascader>\n    </nz-modal>\n\n    <button nz-button (click)=\"open()\">Open Dialog</button>\n    `,\n  styles  : [\n    `\n    .ant-cascader-picker {\n      width: 300px;\n    }\n    `\n  ]\n})\nexport class NzDemoCascaderModalComponent {\n  /** init data */\n  public nzOptions = options;\n\n  /** ngModel value */\n  public values: any[] = null;\n\n  public isVisible = false;\n\n  public onChanges(values: any): void {\n    console.log(values, this.values);\n  }\n\n  public open(): void {\n    this.isVisible = true;\n  }\n\n  handleOk($event: MouseEvent): void {\n    console.log('Button ok clicked!', this.values);\n    this.isVisible = false;\n  }\n\n  handleCancel($event: MouseEvent): void {\n    console.log('Button cancel clicked!', $event);\n    this.isVisible = false;\n  }\n\n}\n"