module.exports = "// tslint:disable:no-any\nimport { Component } from '@angular/core';\n\nconst options = [{\n  value: 'zhejiang',\n  label: 'Zhejiang',\n  children: [{\n    value: 'hangzhou',\n    label: 'Hangzhou',\n    children: [{\n      value: 'xihu',\n      label: 'West Lake',\n      code: 752100,\n      isLeaf: true\n    }]\n  }, {\n    value: 'ningbo',\n    label: 'Ningbo',\n    code: '315000',\n    isLeaf: true\n  }]\n}, {\n  value: 'jiangsu',\n  label: 'Jiangsu',\n  children: [{\n    value: 'nanjing',\n    label: 'Nanjing',\n    children: [{\n      value: 'zhonghuamen',\n      label: 'Zhong Hua Men',\n      code: 453400,\n      isLeaf: true\n    }]\n  }]\n}];\n\n@Component({\n  selector: 'nz-demo-cascader-custom-render',\n  template: `\n    <nz-cascader\n      [nzLabelRender]=\"renderTpl\"\n      [nzOptions]=\"nzOptions\"\n      [(ngModel)]=\"values\"\n      (ngModelChange)=\"onChanges($event)\">\n    </nz-cascader>\n\n    <ng-template #renderTpl let-labels=\"labels\" let-selectedOptions=\"selectedOptions\">\n      <ng-container *ngFor=\"let label of labels; let i = index; let isLast = last\">\n        <span *ngIf=\"!isLast\">{{label}} / </span>\n        <span *ngIf=\"isLast\">\n          {{label}} (<a href=\"javascript:;\" (click)=\"handleAreaClick($event, label, selectedOptions[i])\"> {{selectedOptions[i].code}} </a>)\n        </span>\n      </ng-container>\n    </ng-template>`,\n  styles  : [\n    `\n    .ant-cascader-picker {\n      width: 300px;\n    }\n    `\n  ]\n})\nexport class NzDemoCascaderCustomRenderComponent {\n  /** init data */\n  nzOptions = options;\n\n  /** ngModel value */\n  public values: any[] = null;\n\n  public onChanges(values: any): void {\n    console.log(values, this.values);\n  }\n\n  handleAreaClick(e: Event, label: string, option: any): void {\n    e.preventDefault();\n    e.stopPropagation();\n    console.log('clicked \\\"', label, '\\\"', option);\n  }\n}\n"