module.exports = "// tslint:disable:no-any\nimport { Component, OnInit } from '@angular/core';\n\nconst options = [{\n  value: 'zhejiang',\n  label: 'Zhejiang',\n  children: [{\n    value: 'hangzhou',\n    label: 'Hangzhou',\n    children: [{\n      value: 'xihu',\n      label: 'West Lake',\n      isLeaf: true\n    }]\n  }, {\n    value: 'ningbo',\n    label: 'Ningbo',\n    isLeaf: true\n  }]\n}, {\n  value: 'jiangsu',\n  label: 'Jiangsu',\n  children: [{\n    value: 'nanjing',\n    label: 'Nanjing',\n    children: [{\n      value: 'zhonghuamen',\n      label: 'Zhong Hua Men',\n      isLeaf: true\n    }]\n  }]\n}];\n\nconst otherOptions = [{\n  value: 'fujian',\n  label: 'Fujian',\n  children: [{\n    value: 'xiamen',\n    label: 'Xiamen',\n    children: [{\n      value: 'Kulangsu',\n      label: 'Kulangsu',\n      isLeaf: true\n    }]\n  }]\n}, {\n  value: 'guangxi',\n  label: 'Guangxi',\n  children: [{\n    value: 'guilin',\n    label: 'Guilin',\n    children: [{\n      value: 'Lijiang',\n      label: 'Li Jiang River',\n      isLeaf: true\n    }]\n  }]\n}];\n\n@Component({\n  selector: 'nz-demo-cascader-basic',\n  template: `\n    <nz-cascader\n      [nzOptions]=\"nzOptions\"\n      [(ngModel)]=\"values\"\n      (ngModelChange)=\"onChanges($event)\">\n    </nz-cascader>\n    &nbsp;\n    <a href=\"javascript:;\" (click)=\"changeNzOptions()\" class=\"change-options\">\n      Change Options\n    </a>\n    `,\n  styles  : [\n    `\n    .ant-cascader-picker {\n      width: 300px;\n    }\n    .change-options {\n      display: inline-block;\n      font-size: 12px;\n      margin-top: 8px;\n    }\n    `\n  ]\n})\nexport class NzDemoCascaderBasicComponent implements OnInit {\n  /** init data */\n  public nzOptions = null;\n\n  /** ngModel value */\n  public values: any[] = null;\n\n  ngOnInit(): void {\n    // let's set nzOptions in a asynchronous way\n    setTimeout(() => {\n        this.nzOptions = options;\n    }, 100);\n  }\n\n  public changeNzOptions(): void {\n    if (this.nzOptions === options) {\n      this.nzOptions = otherOptions;\n    } else {\n      this.nzOptions = options;\n    }\n  }\n\n  public onChanges(values: any): void {\n    console.log(values, this.values);\n  }\n}\n"